import Vue from 'vue'
import VueRouter from 'vue-router'
//import store from './store';

Vue.use(VueRouter)


const AdminMain = () => import('@/components/Admin/Static/Main')
const UserMain = () => import('@/components/User/Static/Main')
const Panel = () => import('@/components/Admin/Panel')
const UserPanel = () => import('@/components/User/Panel')
const Login = () => import('@/components/Login')
const Addprojects = () => import('@/components/Admin/Addprojects')
const Addbom = () => import('@/components/Admin/Addbom')
const Editbom = () => import('@/components/Admin/Editbom')
const Bomagaci = () => import('@/components/Admin/Bomagaci')
const Adduser = () => import('@/components/Admin/Adduser')
const Dataimport = () => import('@/components/Admin/Dataimport')
const Profil = () => import('@/components/Profil')
const UserContent=()=> import('@/components/User/Content')
const router = new VueRouter({
  routes: [
    {
      path: '/',
      name: 'Home',
      meta: {
        requiresAuth: true,
        home:true
      }
    },


    {
      path: '/admin',
      name: 'AdminHome',
      redirect:'/admin/panel',
      component: AdminMain,
      children: [
        {
          path: '/admin/panel',
          name: 'AdminPanel',
          component: Panel,
        
        },
        {
          path: '/admin/addprojects',
          name: 'Addprojects',
          component: Addprojects,
        },
        {
          path: '/admin/addbom',
          name: 'Addbom',
          component: Addbom,
        },
        {
          path: '/admin/editbom',
          name: 'Editbom',
          component: Editbom,
        },
        {
          path: '/admin/bomagaci',
          name: 'Bomagaci',
          component: Bomagaci,
        },
        {
          path: '/admin/adduser',
          name: 'Adduser',
          component: Adduser,
        },
        {
          path: '/admin/dataimport',
          name: 'Dataimport',
          component: Dataimport,
        },
        {
          path: '/admin/profil',
          name: 'Profil',
          component: Profil,
        },
        

      ],
      meta: {
        requiresAuth: true,
        is_admin:true
      }
    
      
  
    },

    
    {
      path: '/user',
      name: 'UserHome',
      redirect:'/user/panel',
      component: UserMain,
      children: [
        {
          path: '/user/panel',
          name: 'UserPanel',
          component: UserPanel,
        
        },
        {
          path: '/user/data/:id',
          name: 'Usercontent',
          component: UserContent,
          props: (route) => {id: route.params.articleId}
        
        },
       
        {
          path: '/user/profil',
          name: 'UserProfil',
          component: Profil,
        
        },
      ],
      meta: {
        requiresAuth: true,
        is_user:true
      }
    
      
  
    },
   
  {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
          guest: true
      }
  },
   
  ]

});

router.beforeEach((to, from, next) => {
  if(to.matched.some(record => record.meta.requiresAuth)) {
      if (localStorage.getItem('token') == null) {
          next({
              path: '/login',
              params: { nextUrl: to.fullPath }
          })
      } else {
        Vue.prototype.$http.defaults.headers.common['Authorization'] = "Bearer "+localStorage.getItem('token');

          let user = JSON.parse(localStorage.getItem('user'));
          if(to.matched.some(record => record.meta.home)) {
              if(user.Priority == 1){
                  next({ name: 'AdminHome'})
              }
              else{
                  next({ name: 'UserHome'})
              }
          }
          else if(to.matched.some(record => record.meta.is_admin)){
            if(user.Priority == 1){
              next()
          }
          else{
              next({ name: 'Home'})
          }
          }
          else if(to.matched.some(record => record.meta.is_user)){
            if(user.Priority == 2){
              next()
          }
          else{
              next({ name: 'Home'})
          }
          }
          
          else {
              next()
          }
      }
  } else if(to.matched.some(record => record.meta.guest)) {
      if(localStorage.getItem('token') == null){
          next()
      }
      else{
          next({ name: 'Home'})
      }
  }else {
      next()
  }
})

export default router;